export default {
    name: 'popup', // 
    data() {
        return {
            // 
          text:''
        }
    },
    watch: {
        // 
      'tipText':function(newValue,oldValue){
        this.text=newValue
      }
    },
    props: {
        // 
        popupType:{
          type:String
        },
        popupShow:{
            default:false
        },
        tipText:{
            default:''
        },
        sureTxt:{
            default:''
        },
        cancelTxt:{
            default:''
        }
    },
    computed:{
      txt(){
        return this.tip1Text
      }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    methods: {
        //  
        cancelFn(){//
            this.$parent.mask=false;
            this.$emit('quxiao',{data:false})
        },
        sure(){
        	this.$parent.mask=false;
        	this.$emit('sure',{data:true})
        },
    }

}




