import { Parise } from '@/api/index'
export default {
  name: 'clock', // 
  data() {
    return {
      // 
      userid:localStorage.getItem('userid'),

    }
  },
  props: {
    // 
    clocklist:{
      type:Array
    },
  },
  methods: {
    //
    jumpDetail(id,userid){
      this.$router.push({
        path:'/cardDetail',
        query:{
          id:id,
          userid:userid
        }
      })
    },
    //  
    fullClick(i){
      let obj=this.clocklist[i];
      if(obj.fullTxt==''){
        obj.isFull=false;
        obj.fullTxt='';
      }else{
        obj.isFull=true;
        obj.fullTxt='';
      }
      this.$set(this.clocklist,i,obj);
    },
    isPariseFn(i){
      let obj=this.clocklist[i];
      obj.isParise=true;
      // if(this.name==''){
      //   return false
      // }
      let id=this.clocklist[i].pkid;
      let that=this;
      Parise({logId:id}).then(res =>{
        if(res.msg==''){
          if(that.strInArr(obj.praise,res.data)){
            obj.praise.push(res.data);
          }
          that.$set(that.clocklist,i,obj);
        }else{
          that.$toast('')
          obj.isParise=false;
        }
      })
    },
    strInArr(arr,str){
      if(arr.length==0){
        return true
      }
      for(let x of arr){
        if(x==str){
          return false
        }else{
          return true
        }
      }
    },
  }

}




