import { groupsDetail,Addgroup,queryList } from '@/api/index'
import util from "@/util/util"
export default {
    name: 'applyEntry', // 
    data() {
        return {
            // 
            id:'',//id
            name:'',//
            imgurl:'',//
            num:'',//
            applyTxt:'',
            mask:false,
            toastTxt:'',
            showQrcode:false
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.id=this.$route.query.id;
        let data=localStorage.getItem('isConcern');
        data=JSON.parse(data);
        this.imgurl=data.imgUrl;
        this.num=data.userCount;
        this.name=data.groName;
        if(data.isConcern==0){
            this.showQrcode=true;
        }
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
       
        // console.group('===============mounted');
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        applyGroup(){
            Addgroup({groId:this.id}).then( res => {
                if(res.code == 1 ) {
                    this.applyTxt='';
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                        this.$router.replace({
                            path:'/nav/card'
                        })
                    }, 1500);
                }else if(res.code==403){
                    this.toastTxt=res.msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }else{
                    this.toastTxt=res.msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }
            })
        },
        followFn(){//
            let appid=this.appid;
            let url=encodeURIComponent(location.href.split('?')[0].split('#')[0]+'?path=applyEntry&id='+this.id+'&istrue=1');
            util.weixinauth(appid,url);
        }   
    }

}




